.learnabout{
    background: url("../../assets/learn-about-bg.png");
    background-size: cover;
    border: #9f48f9 5px solid;
    padding: 15px;
}

h2{
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    font-size: 30px;
    color: #000000;
    text-transform: uppercase;
    text-align: left;
}
.learnabout .button{
    width: 100%;
    border: 1px solid #000000;
    padding: 15px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    text-align: left;
    position: relative;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
    margin-bottom: 10px;
    cursor: pointer;
    text-transform: uppercase;
}
.learnabout .button:hover{
    font-size: 19px;
    color: #ffffff;
    border: 2px solid #000000;
}
.learnabout .button:before{
    content: "";
    position: absolute;
    z-index: 1;
    right: 5px;
    bottom: 5px;
    width: 0;
    height: 0;
    border-bottom: 20px solid #9f48f9;
    border-left: 20px solid transparent;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
}
.learnabout .button:hover:before{
    width: 100%;
    height: 100%;
    right: 0;
    bottom: 0;
    background-color: #9f48f9;
}
.buttonText{
    z-index: 10;
    display:block; 
    position:relative;
}


